import React from 'react';
import {Link} from 'react-router-dom';
import dummy from './kurly.json';
import './css/list.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faCommentDots} from "@fortawesome/free-regular-svg-icons";

function ProductChild(props) {
  return (
    <>
    <h2 className='list_h2'>  ?</h2>
    <ul className='list'>
      {dummy.foods.map(product=>
        (
        
          <li key={product.filename}>
            <Link to='/ProductDetail2'>
              <img src={`${process.env.PUBLIC_URL}/images/${product.filename}`} alt="" />
            </Link>
            <img src={`${process.env.PUBLIC_URL}/images/cart.svg`} alt="" className='cart'/>
            <p>
              <Link to='/ProductDetail2'>
                {product.name}
              </Link>
            </p>
            <strong>  
              {product.price}
            </strong>
            <span>
              <FontAwesomeIcon icon={faCommentDots} />  {product.review}
            </span> 
          </li>
        )
      )}
    </ul>
    </>
  );
}

export default ProductChild;