import React from 'react';
import {Link} from 'react-router-dom';

function Header(props) {
  return (
    <>
      <header>
        <h1>
          <Link to='/'>
            <img src={`${process.env.PUBLIC_URL}/images/logo.svg`} alt="" />
          </Link>
        </h1>

        <div>
          <p> &#x007C; </p>
        </div>

        <nav>
          <ul>
            <li>
              <Link to='/Login'></Link>
            </li>
          </ul>
        </nav>
        
        <nav>
          <ul>
            <li>
              <Link to='/NewProduct'></Link>
            </li>
            <li>
              <Link to='/Best'></Link>
            </li>
            <li>
              <Link to='/FleaMarket'></Link>
            </li>
            <li>
              <Link to='/Sale'> / </Link>
            </li>
          </ul>
        </nav>
      </header>
    </>
  );
}

export default Header;