import React, {useState} from 'react';
import './css/login.css';

function Login(props) {

    //state
    const [inputs, setinputs] = useState({
      username:'',
      userid:''
    });
  
    //     .
    const {username, userid} = inputs;
  
    //   -  2
    const onDataChange = (e) => {
      const{value, name} = e.target;
      setinputs({
        ...inputs, // inputs  .
        [name]:value //name    value .
      });
    }
    
    // 
    const onDataReset=()=>{
      setinputs({
        username:'',
        userid:''
      });
    }

  return (
    <>
      <article className='login'>
        <h2></h2>
        <div>
          <input type="text" placeholder='' name='username' value={username} onChange={onDataChange}/>
          <input type="text" placeholder='' name='userid' value={userid} onChange={onDataChange} />

          <p>
            <a href="#none" title=' '>  </a>
            <a href="#none" title=' '>|  </a>
          </p>

          <input type="submit" value='' />
          <input type="button" value='' />

          <button onClick={onDataReset}> </button>
          <p> : {username}({userid})</p>
        </div>
      </article>
    </>
  );
}

export default Login;